@charset "UTF-8";
:root {
  --white-color: #fff;
  --max-width-container: calc(1254px + 30px); }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  background-color: var(--bg-color);
  color: var(--white-color);
  font-family: "Roboto", sans-serif;
  font-size: 20px; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: inherit; }

.visually-hidden {
  position: absolute !important;
  /*удаляет элемент из потока документа*/
  overflow: hidden;
  /*cкрывает вылезающий за границы контент*/
  clip: rect(1px 1px 1px 1px);
  /*Вырезает ту часть контента, которая должна отображаться. Для IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  /*вырезает ту часть контента, которая должна отображаться*/
  width: 1px;
  /*минимально возможный размер ширины (некоторые скринридеры игнорируют элементы с нулевой высотой и шириной) */
  height: 1px;
  /*минимально возможный размер высоты (некоторые скринридеры игнорируют элементы с нулевой высотой и шириной) */
  padding: 0;
  /*убирает отступы*/
  border: 0;
  /*убирает границы*/ }

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0; }

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer; }

.site-container {
  overflow: hidden; }

.js-validate-error-field {
  border: 3px solid var(--gold-color) !important;
  color: var(--gold-color) !important; }

.js-validate-error-label {
  position: absolute;
  top: -17px;
  left: 5px;
  font-size: 14px;
  color: var(--gold-color) !important; }

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: var(--max-width-container); }

@media (max-width: 1284px) {
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1130px; } }

@media (max-width: 1130px) {
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 930px; } }

@media (max-width: 930px) {
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 630px; } }

@media (max-width: 630px) {
  .container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 400px; } }

@media (max-width: 400px) {
  .container {
    margin: 0 auto;
    padding: 0 10px;
    max-width: 300px; } }

.btns-menu {
  width: 100%;
  padding: 10px 15px;
  margin-bottom: 45px;
  background-color: rgba(255, 255, 255, 0.4); }

.btn {
  position: relative;
  padding: 5px 15px;
  font-size: 18px;
  font-weight: 700;
  border: none;
  cursor: pointer;
  color: var(--white-color);
  text-transform: uppercase; }

.btn:after {
  content: " ";
  position: absolute;
  height: 100%;
  width: 0;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.4);
  transition: all 0.7s ease;
  mix-blend-mode: overlay; }

.btn:hover::after {
  width: 100%; }

.btn:not(:last-child) {
  margin-right: 10px; }

.btn:disabled {
  opacity: 0.4;
  cursor: default; }

.btn:disabled:hover::after {
  width: 0; }

.home {
  margin-right: 10px;
  display: inline-block;
  background-color: #333;
  padding: 5px 15px;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  color: var(--white-color);
  text-transform: uppercase; }

.tic-tac-toe {
  background: #56b4d3;
  background: linear-gradient(65deg, #56b4d3 7%, #795fa3 35%, #c15746 90%);
  height: 100vh; }
  .tic-tac-toe__box {
    display: flex;
    align-items: center;
    flex-direction: column; }
  .tic-tac-toe__btn {
    background-color: #1c6d87; }
  .tic-tac-toe__restart {
    background-color: #c15746; }
  .tic-tac-toe__game {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    margin-bottom: 25px; }
  .tic-tac-toe__cell {
    width: 80px;
    height: 80px;
    background-color: rgba(255, 255, 255, 0.4); }
  .tic-tac-toe__win-text {
    margin: 0;
    font-family: "Roboto", serif;
    font-size: 40px;
    text-align: center;
    color: var(--white-color);
    font-weight: 900;
    text-shadow: 3px 3px 7px rgba(0, 0, 0, 0.5); }

.mouse {
  cursor: pointer; }

.circle {
  width: 100%;
  height: 100%;
  stroke-dasharray: 283;
  stroke-dashoffset: 283;
  animation: draw 1s 1.5s forwards; }

.cross {
  width: 100%;
  height: 100%; }

.cross-first {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  animation: draw 0.6s forwards; }

.cross-second {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  animation: draw 0.6s 0.6s forwards; }

@keyframes draw {
  100% {
    stroke-dashoffset: 0; } }

.snake {
  height: 100vh;
  background: #dce35b;
  background: linear-gradient(36deg, #dce35b 0%, #45b649 100%); }
  .snake__restart {
    background-color: #644172; }
  .snake__btn {
    background-color: #8E941E; }
